.home_container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.home_container h1 {
    font-size: 2.5em;
    margin-bottom: .5em;
    text-align: center;
}

.home_container h1 span {
    color: #1976d2;
    padding: 0 .2em;
}

.home_container p {
    margin-bottom: 1.5em;
    color: #7b7b7b;
}

.home_container img {
    width: 350px;
    margin: 2em 0;
}